@tailwind base;
@tailwind components;
@tailwind utilities;

.bg-ios-light-surface {
    background: #fff;
}

.rounded {
    border-radius: 1rem;
}

.gradient {
    background: linear-gradient(180deg, #FFB9B9 0%, rgba(255, 185, 185, 0) 100%);
    border-radius: 3rem;
}

.dashboard__avatar {
    background: #FCEBED;
    @apply rounded;
}

.dashboard__healthy {
    background: #FFF0CD;
    @apply rounded;
}

.dashboard__healthy__index {
    @apply p-4 rounded;
    background: #fff;
    box-shadow: 0px 0px 13px 3px rgba(81, 16, 32, 0.05);
}

.dashboard__healthy__coin {
    @apply p-4 rounded;
}

.k-tabbar-link-active {
    @apply font-extrabold;
    filter: invert(95%) sepia(36%) saturate(3071%) hue-rotate(290deg) brightness(106%) contrast(105%);
}

.water-widget__button {
    cursor: pointer;
}

.k-color-brand-red {
    color: red;
}

.progress-bar span {
    background: #FFB9B9;
}